*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-inter);
  background-color: var(--dark-bg);
  color: var(--dark-white-text);
}

img {
  display: block;
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.25rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram: linear-gradient(hsl(37, 97%, 70%), hsl(329, 70%, 58%));
  --youtube: hsl(348, 97%, 39%);
  --dark-toggle-start: hsl(210, 78%, 56%);
  --dark-toggle-end: hsl(146, 68%, 55%);
  --light-toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-blue-text: hsl(228, 34%, 66%);
  --dark-white-text: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-gray-text: hsl(228, 12%, 44%);
  --light-blue-text: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  max-width: 68.75rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.card {
  padding: 1.25rem;
}

.toggle {
  border: none;
  border-top: 2px solid var(--dark-card);
  display: flex;
  gap: 1.125rem;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5rem;
  padding-top: 1rem;
  cursor: default;
}
@media (min-width: 40em) {
  .toggle {
    border: none;
    margin-top: 0;
    padding-top: 0;
  }
}
.toggle__wrapper {
  height: 1.5rem;
  width: 3rem;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -2px 0 0;
  width: 1.25rem;
  height: 1.25rem;
}
.toggle__background {
  display: block;
  background: linear-gradient(225deg, var(--dark-toggle-end), var(--dark-toggle-start));
  border-radius: 0.75rem;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--dark-card);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 1.3125rem);
}
.toggle label[for=dark] {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-white-text);
}
.toggle label[for=light] {
  position: absolute;
  width: 1px;
  height: 1px;
  left: -10000px;
  opacity: 0;
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-blue-text);
}